<template>
    <view class="container">
		<u-row justify="space-between" gutter="5" class="search-content">
		    <u-col span="3">
				<u-popup :show="addressshow" mode="bottom" :round="10" @close="closeAddress" @open="openAddress">
						<view  class="pop_check_address">
							<text>人生若只如初见，何事秋风悲画扇</text>
						</view>
				</u-popup>
		        <view class="demo-layout bg-purple" @click="selectAddress">地点</view>
		    </u-col>
		    <u-col span="3">
				<u-popup :show="carModelshow" mode="bottom" :round="10" @close="closeCarModel"
				 @open="openCarModel">
						<view  class="pop_check_address">
							<text>人生若只如初见，何事秋风悲画扇</text>
						</view>
				</u-popup>
		        <view class="demo-layout bg-purple-light" @click="selectCarModel">车型</view>
		    </u-col>
		    <u-col span="3">
				<u-popup :show="priceshow" mode="bottom" :round="10" @close="closePrice"
				 @open="openPrice">
						<view  class="pop_check_address">
							<text>人生若只如初见，何事秋风悲画扇</text>
						</view>
				</u-popup>
		        <view class="demo-layout bg-purple" @click="selectPrice">价格</view>
		    </u-col>
		    <u-col span="3">
				<u-popup :show="xuHangshow" mode="bottom" :round="10" @close="closexuHang"
				 @open="openxuHang">
						<view  class="pop_check_address">
							<text>人生若只如初见，何事秋风悲画扇</text>
						</view>
				</u-popup>
		        <view class="demo-layout bg-purple-light" @click="selectXuHang">续航里程</view>
		    </u-col>
		</u-row>
       
		<view class="scroll-panel" id="scroll-panel">
            <view class="list-box">
                <view class="left">
                    <scroll-view scroll-y="true" :style="{ height: scrollHeight + 'px' }" :scroll-into-view="leftIntoView">
                        <view
                            class="item"
                            v-for="(item, index) in leftArray"
                            :key="index"
                            :class="{ active: index == leftIndex }"
                            :id="'left-' + index"
                            :data-index="index"
                            @tap="leftTap"
                        >
                        <view class="activelink"></view>
                         <text class="item-name">{{ item }}</text> 
                        </view>
                    </scroll-view>
                </view>
                <view class="main">
                    <scroll-view scroll-y="true" :style="{ height: scrollHeight + 'px' }" @scroll="mainScroll" :scroll-into-view="scrollInto" scroll-with-animation="true">
                        <view class="item main-item" v-for="(item, index) in mainArray" :key="index" :id="'item-' + index">
                            <view class="title">
                                <view>{{ item.title }}</view>
                            </view>
                            <view class="goods" v-for="(item2, index2) in item.list" :key="index2">
                                <view class="orderlist-list">
                                    <view class="list-left">
                                        <image src="../../static/logo.png" mode=""></image>
                                    </view>
                                    <view class="list-right">
                                        <view class="list-name">品牌：香辣火锅</view>
                                        <view class="list-name">型号：香辣火锅</view>
                                        <view class="list-name">价格：香辣火锅</view>
                                    </view>
                                </view>
                            </view>
                        </view>
                        <view class="fill-last" :style="{ height: fillHeight + 'px' }"></view>
                    </scroll-view>
                </view>
            </view>
        </view>
		
    </view>
    
</template>
<script>
export default{
    data() {
            return {
				addressshow:false,
				carModelshow:false,
				priceshow:false,
				xuHangshow:false,
                scrollHeight: 400,
                scrollTopSize: 0,
                fillHeight: 0, // 填充高度，用于最后一项低于滚动区域时使用
                leftArray: [],
                mainArray: [],
                topArr: [],
                leftIndex: 0,
                scrollInto: '',
                count: 5,
                value: 4
            };
        },
        computed: {
            /* 计算左侧滚动位置定位 */
            leftIntoView() {
                return `left-${this.leftIndex > 3 ? this.leftIndex - 3 : 0}`;
            }
        },
        mounted() {
            /* 等待DOM挂载完成 */
            this.$nextTick(() => {
                /* 在非H5平台，nextTick回调后有概率获取到错误的元素高度，则添加200ms的延迟来减少BUG的产生 */
                setTimeout(() => {
                    /* 等待滚动区域初始化完成 */
                    this.initScrollView().then(() => {
                        /* 获取列表数据，你的代码从此处开始 */
                        this.getListData();
                    });
                }, 200);
            });
        },
        methods: {
            /* 初始化滚动区域 */
            initScrollView() {
                return new Promise((resolve, reject) => {
                    let view = uni.createSelectorQuery().select('#scroll-panel');
                    view.boundingClientRect(res => {
                        this.scrollTopSize = res.top;
                        this.scrollHeight = res.height;
                        this.$nextTick(() => {
                            resolve();
                        });
                    }).exec();
                });
            },
            /* 获取列表数据 */
            getListData() {
                // Promise 为 ES6 新增的API ，有疑问的请自行学习该方法的使用。
                new Promise((resolve, reject) => {
                    /* 因无真实数据，当前方法模拟数据。正式项目中将此处替换为 数据请求即可 */
                    uni.showLoading();
                    setTimeout(() => {
                        let [left, main] = [[], []];
    
                        for (let i = 0; i < 12; i++) {
                            left.push(`${i + 1}类商品`);
    
                            let list = [];
                            let r = Math.floor(Math.random() * 10);
                            r = r < 1 ? 3 : r;
                            for (let j = 0; j < r; j++) {
                                list.push(j);
                            }
                            main.push({
                                title: `第${i + 1}类商品标题`,
                                list
                            });
                        }
    
                        // 将请求接口返回的数据传递给 Promise 对象的 then 函数。
                        resolve({ left, main });
                    }, 1000);
                }).then(res => {
                    console.log('-----------请求接口返回数据示例-------------');
                    console.log(res);
    
                    uni.hideLoading();
                    this.leftArray = res.left;
                    this.mainArray = res.main;
    
                    // DOM 挂载后 再调用 getElementTop 获取高度的方法。
                    this.$nextTick(() => {
                        this.getElementTop();
                    });
                });
            },
            /* 获取元素顶部信息 */
            getElementTop() {
                new Promise((resolve, reject) => {
                    let view = uni.createSelectorQuery().selectAll('.main-item');
                    view.boundingClientRect(data => {
                        resolve(data);
                    }).exec();
                }).then(res => {
                    let topArr = res.map(item => {
                        return item.top - this.scrollTopSize; /* 减去滚动容器距离顶部的距离 */
                    });
                    this.topArr = topArr;
    
                    /* 获取最后一项的高度，设置填充高度。判断和填充时做了 +-20 的操作，是为了滚动时更好的定位 */
                    let last = res[res.length - 1].height;
                    if (last - 20 < this.scrollHeight) {
                        this.fillHeight = this.scrollHeight - last + 20;
                    }
                });
            },
            /* 主区域滚动监听 */
            mainScroll(e) {
                let top = e.detail.scrollTop;
                let index = 0;
                /* 查找当前滚动距离 */
                for (let i = this.topArr.length - 1; i >= 0; i--) {
                    /* 在部分安卓设备上，因手机逻辑分辨率与rpx单位计算不是整数，滚动距离与有误差，增加2px来完善该问题 */
                    if (top + 2 >= this.topArr[i]) {
                        index = i;
                        break;
                    }
                }
                this.leftIndex = index < 0 ? 0 : index;
            },
            /* 左侧导航点击 */
            leftTap(e) {
                let index = e.currentTarget.dataset.index;
                this.scrollInto = `item-${index}`;
            },
			selectAddress(){
				this.addressshow=true
			},
			selectCarModel(){
				this.carModelshow=true
			},
			selectPrice() {
			  this.priceshow = true
			},
			selectXuHang() {
			  this.xuHangshow = true
			},
			closexuHang(){
			 this.xuHangshow = false
			},
			 closeAddress() {
			  this.addressshow = false
			}, 
			closeCarModel() {
			  this.carModelshow = false
			},
			closePrice() {
			  this.priceshow = false
			}
			
        }
    };
</script>
